// ...existing code...
import { useState, useEffect } from 'react';
import './Account.css';
import './AccountForm.css';
import { useProfileStore } from '../../stores/profileStore.js';
import { toast } from 'react-toastify';

export default function ProfileForm({ handleCloseForm }) {
  const { updateUserProfile, profile, loading, address } = useProfileStore();

  const [formData, setFormData] = useState({
    firstName: profile?.firstName || '',
    lastName: profile?.lastName || '',
    imageUrl: null,
    sendEmailNotifications:
      profile?.sendEmailNotifications !== undefined ? profile.sendEmailNotifications : true,
    addressLine1: '',
    addressLine2: '',
    city: '',
    state: '',
    postalCode: '',
    countryCode: 'US',
  });
  const [showAddress, setShowAddress] = useState(false);
  const [previewImage, setPreviewImage] = useState(profile?.imageUrl || null);

  // Helper: check if any required address field is filled
  const requiredAddressFields = ['addressLine1', 'city', 'state', 'postalCode'];

  const allAddressFieldsFilled = requiredAddressFields.every(
    (field) => formData[field].trim() !== ''
  );

  // Also highlight when no address is provided at all
  const needsAttention = !allAddressFieldsFilled; // true for partial OR empty

  // Button style values (avoid long ternaries inline)
  const addressBtnBackground = needsAttention ? '#e74c3c' : '#fffbe6';
  const addressBtnColor = needsAttention ? '#fff' : '#222';
  const addressBtnWeight = needsAttention ? 700 : 500;
  const countries = [
    { code: 'US', name: 'United States' },
    { code: 'CA', name: 'Canada' },
    { code: 'GB', name: 'United Kingdom' },
    { code: 'AU', name: 'Australia' },
    { code: 'DE', name: 'Germany' },
    { code: 'FR', name: 'France' },
    { code: 'ES', name: 'Spain' },
    { code: 'IT', name: 'Italy' },
    { code: 'NL', name: 'Netherlands' },
    { code: 'SE', name: 'Sweden' },
    { code: 'NZ', name: 'New Zealand' },
    { code: 'JP', name: 'Japan' },
  ];

  // Update form data when profile changes
  useEffect(() => {
    if (profile) {
      setFormData((prev) => ({
        ...prev,
        firstName: profile.firstName || '',
        lastName: profile.lastName || '',
        imageUrl: null,
        sendEmailNotifications: profile.sendEmailNotifications,
        addressLine1: address?.addressLine1 || '',
        addressLine2: address?.addressLine2 || '',
        city: address?.city || '',
        state: address?.state || '',
        postalCode: address?.postalCode || '',
        countryCode: address?.countryCode || 'US',
      }));
      setPreviewImage(profile.imageUrl || null);
    }
  }, [profile, address]);

  // Validation functions
  const validateName = (name, fieldName) => {
    if (name.length > 50) {
      toast.warn(`${fieldName} must be 50 characters or less`, {
        theme: 'dark',
        draggable: true,
        draggablePercent: 60,
        autoClose: 3000,
      });
    }
  };

  const validateImageSize = (file) => {
    const maxSize = 5 * 1024 * 1024; // 5MB in bytes
    if (file.size > maxSize) {
      toast.warn('Image must be 5MB or less', {
        theme: 'dark',
        draggable: true,
        draggablePercent: 60,
        autoClose: 3000,
      });
      return false;
    }
    return true;
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    // Sanitize city autocomplete that may include state (e.g. "Eugene, OR")
    if (name === 'city') {
      const cityStatePattern = /^(.*?),(\s*)([A-Za-z]{2})$/; // captures City, ST
      const match = value.match(cityStatePattern);
      if (match) {
        const parsedCity = match[1].trim();
        const parsedState = match[3].toUpperCase();
        setFormData((prev) => ({
          ...prev,
          city: parsedCity,
          state: prev.state ? prev.state : parsedState,
        }));

        return;
      }
    }

    // If user manually typed state in state field with lowercase, normalize
    if (name === 'state') {
      const normalized = value.toUpperCase().trim();
      // Only keep first two letters if user pasted longer region code for US
      setFormData((prev) => ({
        ...prev,
        state: normalized.length > 2 ? normalized.slice(0, 2) : normalized,
      }));
      return;
    }

    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));

    if (name === 'firstName') {
      validateName(value, 'First name');
    } else if (name === 'lastName') {
      validateName(value, 'Last name');
    }
  };

  const handleImageChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      if (validateImageSize(file)) {
        setFormData((prev) => ({
          ...prev,
          imageUrl: file,
        }));

        // Create preview
        const reader = new FileReader();
        reader.onload = (e) => {
          setPreviewImage(e.target.result);
        };
        reader.readAsDataURL(file);
      }
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      // Submit-time address validation: all-or-nothing
      const required = ['addressLine1', 'city', 'state', 'postalCode'];
      const allFilled = required.every((f) => (formData[f] || '').trim() !== '');

      let addressPayload = {};
      if (allFilled) {
        addressPayload = {
          addressLine1: formData.addressLine1,
          addressLine2: formData.addressLine2 || '',
          city: formData.city,
          state: formData.state,
          postalCode: formData.postalCode,
          countryCode: formData.countryCode || 'US',
        };
      }

      await updateUserProfile({
        firstName: formData.firstName,
        lastName: formData.lastName,
        file: formData.imageUrl,
        existingImageUrl: profile?.imageUrl || null,
        sendEmailNotifications: formData.sendEmailNotifications,
        ...addressPayload,
      });

      handleCloseForm();
    } catch (error) {
      console.error('Error updating profile:', error);
      // toast shown by store
    }
  };

  // Close on Escape key
  useEffect(() => {
    const handleKey = (e) => {
      if (e.key === 'Escape') {
        handleCloseForm();
      }
    };
    window.addEventListener('keydown', handleKey);
    return () => window.removeEventListener('keydown', handleKey);
  }, [handleCloseForm]);

  // Lock background scroll while modal is open
  useEffect(() => {
    const prevHtmlOverflow = document.documentElement.style.overflow;
    const prevBodyOverflow = document.body.style.overflow;
    document.documentElement.style.overflow = 'hidden';
    document.body.style.overflow = 'hidden';
    document.body.classList.add('modal-open');
    return () => {
      document.body.classList.remove('modal-open');
      document.documentElement.style.overflow = prevHtmlOverflow;
      document.body.style.overflow = prevBodyOverflow;
    };
  }, []);

  return (
    <div className="profile-form-overlay">
      <div className="profile-form-container">
        <div className="profile-form-header">
          <h2>Edit Profile</h2>
          <button className="close-btn" onClick={handleCloseForm}>
            
          </button>
        </div>

        <form onSubmit={handleSubmit} className="profile-form">
          <div className="form-group">
            <label htmlFor="firstName">First Name</label>
            <input
              type="text"
              id="firstName"
              name="firstName"
              value={formData.firstName || ''}
              onChange={handleInputChange}
              placeholder="Enter your first name"
              maxLength={51}
            />
          </div>

          <div className="form-group">
            <label htmlFor="lastName">Last Name</label>
            <input
              type="text"
              id="lastName"
              name="lastName"
              value={formData.lastName || ''}
              onChange={handleInputChange}
              placeholder="Enter your last name"
              maxLength={51}
            />
          </div>

          <div className="form-group">
            <label htmlFor="imageUrl">Profile Picture</label>
            <div className="image-upload-container">
              {previewImage && (
                <div className="image-preview">
                  <img src={previewImage} alt="Profile preview" />
                </div>
              )}
              <input
                type="file"
                id="imageUrl"
                name="imageUrl"
                accept="image/*"
                onChange={handleImageChange}
                className="file-input"
              />
              <label htmlFor="imageUrl" className="file-input-label">
                {formData.imageUrl ? 'Change Image' : 'Choose Image'}
              </label>
            </div>
          </div>

          <div
            className="form-group"
            style={{ border: '1px solid yellow ', padding: '.5rem', borderRadius: '8px' }}
          >
            <label>
              <input
                type="checkbox"
                name="sendEmailNotifications"
                checked={formData.sendEmailNotifications}
                onChange={(e) =>
                  setFormData((prev) => ({
                    ...prev,
                    sendEmailNotifications: e.target.checked,
                  }))
                }
              />
              Receive email notifications for new work/ auctions
            </label>
          </div>

          {/* Shipping Address Section */}
          <div className="form-group address-section">
            <button
              type="button"
              className="toggle-address-btn"
              onClick={() => setShowAddress((prev) => !prev)}
              style={{
                marginBottom: '0.5rem',
                background: addressBtnBackground,
                border: '1px solid #ccc',
                borderRadius: '6px',
                padding: '0.5rem 1rem',
                cursor: 'pointer',
                color: addressBtnColor,
                fontWeight: addressBtnWeight,
                transition: 'background 0.2s',
              }}
            >
              {showAddress ? 'Hide Shipping Address' : 'Add shipping address'}
            </button>
            {showAddress && (
              <div
                className="address-fields"
                style={{
                  padding: '1rem',
                  borderRadius: '8px',
                  border: '1px solid #ffe066',
                }}
              >
                <div
                  className="address-hint"
                  style={{
                    fontSize: '.8rem',
                    marginBottom: '.5rem',
                    color: needsAttention ? '#e74c3c' : '#555',
                    padding: needsAttention ? '0.25rem 0.5rem' : 0,
                    borderRadius: '4px',
                  }}
                >
                  {needsAttention
                    ? 'Provide all fields if adding an address, or leave all blank. Partial address not accepted.'
                    : 'Things look stokey!'}
                </div>
                <div className="form-group">
                  <label htmlFor="addressLine1">Street Address</label>
                  <input
                    type="text"
                    id="addressLine1"
                    name="addressLine1"
                    value={formData.addressLine1}
                    onChange={handleInputChange}
                    placeholder="123 Main St"
                    autoComplete="address-line1"
                  />
                </div>
                <div className="form-group">
                  <label htmlFor="addressLine2">Address Line 2 (optional)</label>
                  <input
                    type="text"
                    id="addressLine2"
                    name="addressLine2"
                    value={formData.addressLine2}
                    onChange={handleInputChange}
                    placeholder="Apt, suite, etc. (optional)"
                    autoComplete="address-line2"
                  />
                </div>
                <div className="form-group">
                  <label htmlFor="city">City</label>
                  <input
                    type="text"
                    id="city"
                    name="city"
                    value={formData.city}
                    onChange={handleInputChange}
                    placeholder="City"
                    autoComplete="address-level2"
                  />
                </div>

                <div className="form-group">
                  <label htmlFor="state">State</label>
                  <input
                    type="text"
                    id="state"
                    name="state"
                    value={formData.state}
                    onChange={handleInputChange}
                    placeholder="State"
                    autoComplete="address-level1"
                  />
                </div>
                <div className="form-group">
                  <label htmlFor="postalCode">Postal Code</label>
                  <input
                    type="text"
                    id="postalCode"
                    name="postalCode"
                    value={formData.postalCode}
                    onChange={handleInputChange}
                    placeholder="ZIP or postal code"
                    autoComplete="postal-code"
                  />
                </div>
                <div className="form-group">
                  <label htmlFor="countryCode">Country</label>
                  <select
                    id="countryCode"
                    name="countryCode"
                    value={formData.countryCode}
                    onChange={handleInputChange}
                    autoComplete="country-name"
                  >
                    {countries.map((c) => (
                      <option key={c.code} value={c.code}>
                        {c.name}
                      </option>
                    ))}
                  </select>
                </div>
              </div>
            )}
          </div>

          <div className="form-actions">
            <button type="button" onClick={handleCloseForm} className="cancel-btn-account">
              Cancel
            </button>
            <button type="submit" className="submit-btn-account" disabled={loading}>
              {loading ? 'Updating...' : 'Update Profile'}
            </button>
          </div>
        </form>
      </div>
    </div>
  );
}
